

.default:hover {
    background-color: #45a7fc;
}

.default {
    display: inline-block;
    outline: 0;
    border: 0;
    cursor: pointer;
    background-color: #4299e1;
    border-radius: 50px;
    padding: 8px 16px;
    font-size: 16px;
    font-weight: 700;
    color: white;
    line-height: 26px;
}
.disabled{
    background-color: rgba(66, 153, 225, 0.29);
    color: rgba(255, 255, 225, 0.29);
}.disabled:hover{
    background-color: rgba(66, 153, 225, 0.29);
}
.red {
    background: red;
}
